import React, { Component } from 'react';
import './App.css';
import ScreenContext from './ScreenContext';
import Users from './Users';
import Searchit from './Searchit';

// UI framework component imports
import Button from 'muicss/lib/react/button';
import Appbar from 'muicss/lib/react/appbar';

export default class AquaDirectoryScreen extends Component {

  static contextType = ScreenContext;


  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate(prevProps, prevState) {
  }

  
  onClick_elButton = async () => {
    // Go to screen 'Home'
    this.context.appActions.goToScreen('home', this.context.baseScreenId, { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.context.transitionId && this.context.transitionId.length > 0 && this.context.atTopOfScreenStack && this.context.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.context.transitionId;
    }
    if ( !this.context.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    // eslint-disable-next-line
    const dataSheet_emps = this.context.dataSheets['emps'];
    
    const style_elRectangle = {
      background: 'rgba(239, 238, 233, 1.000)',
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    let filterItems_list = items => {
      
      let filteredItems = [];
      let filterStr=this.context.appActions.dataSlots['ds_filter']; // Data slot value
      filterStr=filterStr.toLowerCase(); // Lowercase the filter string
      
      // Loop through all items and add item to new array if item includes filter string
      items.forEach(item => {;
      if (item['First_Name'].toLowerCase().includes(filterStr)) {
      filteredItems.push(item);
      }else if (item['Last_Name'].toLowerCase().includes(filterStr)){
      filteredItems.push(item);
      }else if (item['Dept'].toLowerCase().includes(filterStr)){
      filteredItems.push(item);
      }
      });
      items=filteredItems; 
      
      return items;
    }
    items_list = items_list.concat(filterItems_list(this.context.appActions.getDataSheet('emps').items));
    this._elList_items = [];
    
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    
    const style_elButton = {
      display: 'block',
      color: '#fff',
      textAlign: 'center',
      backgroundColor: 'rgba(0, 0, 0, 0.5000)',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    return (
      <div className="AppScreen AquaDirectoryScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elRectangle" style={style_elRectangle} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Users dataSheetId={'emps'} dataSheetRow={row} {...{ 'Title': row['Title'], 'Ext': row['Ext'], 'Dept': row['Dept'], 'EMailAddress': row['EMailAddress'], 'FullName': row['FullName'], 'Photo': row['Photo'], 'URL': row['URL'], }} ref={(el) => {if (el) this._elList_items.push(el)}} />;
                return (<li key={row.key ||index}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
        </div>
        <div className="navBarContainer">
         <Appbar className="navBar">
          <div className="title">AQUALocator</div> </Appbar>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className="hasNestedComps elSearchit">
              <Searchit ref={(el)=> this._elSearchit = el} />
            </div>
            <Button className="actionFont elButton" style={style_elButton} onClick={this.onClick_elButton} >
              {this.context.locStrings.aquadirectory_button_369045}
            </Button>
          </div>
        </div>
      </div>
    )
  }
  
}
